import { Component, ViewEncapsulation, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute, Params } from '@angular/router';
import { ForgotPasswordService } from './../services/ForgotPasswordService';
@Component({
  selector: 'changepassword',
  styles: [require('./changepassword.style.scss')],
  template: require('./changepassword.template.html'),
   providers: [ForgotPasswordService],
  encapsulation: ViewEncapsulation.None,
})
export class ChangePasswordComponent {
  public Password:any;
  public ConfirmPassword:any;
  public router: Router;
  public UserEmail:any;
  public UserGuid:any;
constructor(private activatedRoute: ActivatedRoute,router: Router,public _forgotPasswordService:ForgotPasswordService)
{

  this.router = router;

}
  submitNewPassword()
  {
  
    if(this.Password==undefined||this.Password=="")
    {
     
      
      jQuery("#rpasswordRequiredError").show();
return;
    }else
    if(this.Password!=this.ConfirmPassword)
    {
         jQuery("#rpasswordRequiredError").hide();
jQuery("#passwordMustMatch").show();
return;
    }else if(!this.checkStringComplexity(this.Password))
    {
  jQuery("#rpasswordRequiredError").hide();
  jQuery("#passwordMustMatch").hide();
jQuery("#passwordMustMatchcom").show();

    }
    else
    {
         jQuery("#passwordMustMatchcom").hide();
              jQuery("#rpasswordRequiredError").hide();
               jQuery("#passwordMustMatch").hide();
                jQuery("#rpasswordRequiredError").hide();
              
this.verifyUser();
    }
  }

  checkStringComplexity(pwd) {
    var letter = /[a-zA-Z]/;
    var number = /[0-9]/;
    var valid = number.test(pwd) && letter.test(pwd); //match a letter _and_ a number
    return valid;
  }
  verifyUser()
  {
      this.activatedRoute.queryParams.subscribe((params: Params) => {
      
      this.UserEmail=params['email'];
      this.UserGuid=params['reset'];
      this._forgotPasswordService.verifyResetRequest(this.UserEmail,this.UserGuid,this.Password).subscribe(

        a=>{

          if(a.code==200)
          {
            console.log("Password Successfully Updated");

          
            jQuery("#passwordMustMatchv").html("Password Successfully Updated");
            this.Password="";
            this.ConfirmPassword="";
             jQuery("#passwordMustMatchv").show();
          }else
          if(a.code==100)
          {
            //console.log("Password Successfully Updated");
            jQuery("#passwordMustMatchv").html("Invalid Reset request");
          }
          else{
           console.log("Error!");
          }
        }
      );
    });


  }

}



